@font-face {
  font-family: "Oswald";
  src: url("../source/fonts/oswaldregular.woff2") format("woff2"), url("../fonts/oswaldregular.woff") format("woff");
  font-weight: 400;
  font-style: normal;
  font-display: swap; }

@font-face {
  font-family: "Oswald";
  src: url("../source/fonts/oswaldmedium.woff2") format("woff2"), url("../fonts/oswaldmedium.woff") format("woff");
  font-weight: 500;
  font-style: medium;
  font-display: swap; }

*,
*::before,
*::after {
  box-sizing: border-box; }

a {
  text-decoration: none; }

.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  padding: 0;
  overflow: hidden;
  border: 0;
  clip: rect(0 0 0 0); }

.container {
  max-width: 1300px;
  margin: 0 auto;
  padding: 0 30px; }
  @media (max-width: 767px) {
    .container {
      padding: 0 5px; } }

.page {
  height: 100%; }

.page__body {
  min-width: 320px;
  min-height: 100%;
  margin: 0;
  padding: 0;
  font-family: "Oswald", "Arial", sans-serif;
  font-style: normal;
  font-weight: normal;
  color: #000000;
  display: grid;
  grid-template-rows: -webkit-min-content 1fr -webkit-min-content;
  grid-template-rows: min-content 1fr min-content; }
  .page__body--index {
    height: 100%; }

.header {
  width: 100%;
  padding-top: 20px; }

.main {
  min-height: 100%;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center; }
  .main--projects {
    justify-content: start; }

.navigation__list {
  display: flex;
  justify-content: center;
  margin: 0;
  padding: 0;
  list-style: none; }

.navigation__item:not(:last-child) {
  margin-right: 10px; }

.navigation__link {
  color: #000000; }
  .navigation__link:hover, .navigation__link:focus {
    color: #008272; }
  .navigation__link:active {
    color: #008272;
    opacity: 0.3; }

.project__list {
  padding: 0;
  margin: 0;
  list-style: none; }

.project__item {
  display: grid;
  -moz-column-gap: 25px;
       column-gap: 25px;
  grid-template-columns: -webkit-min-content 1fr;
  grid-template-columns: min-content 1fr;
  grid-template-rows: -webkit-min-content 1fr;
  grid-template-rows: min-content 1fr;
  padding: 20px;
  border: 1px solid #000000; }
  .project__item:not(:last-child) {
    margin-bottom: 30px; }
  @media (max-width: 767px) {
    .project__item {
      display: flex;
      flex-direction: column;
      padding: 10px; } }

.project__img {
  grid-row: 1/-1; }
  @media (max-width: 767px) {
    .project__img {
      margin-bottom: 10px;
      text-align: center; } }

.project__link {
  color: #000000; }
  .project__link--desc {
    text-decoration: underline; }
  .project__link:hover, .project__link:focus {
    color: #008272; }
  .project__link:active {
    color: #008272;
    opacity: 0.3; }

.project__desc {
  font-family: "Arial", sans-serif; }

.project__technology {
  font-family: "Arial", sans-serif; }

.social {
  display: flex;
  justify-content: center;
  align-items: center;
  height: 25px; }

.social__list {
  display: flex;
  justify-content: space-around;
  align-items: center;
  width: 130px;
  list-style: none;
  margin: 0;
  padding: 0; }

.social__item {
  display: flex;
  justify-content: center;
  align-items: center; }

.social__img {
  fill: #000000; }

.social__link:hover .social__img, .social__link:focus .social__img {
  fill: #008272; }

.social__link:active .social__img {
  fill: rgba(0, 130, 114, 0.7); }

.footer {
  width: 100%; }
